<template>
    <div id="app">
        <div class="todo">
            <h1>todo</h1>
            <div class="addarea">
                <input
                    type="text"
                    v-model="addText"
                    placeholder="Add a new todo"
                />
                <button
                    @click="addItem"
                >Add</button>
            </div>
            <ul v-if="todos.length">
                <li
                    v-for="todo in todos"
                    :key="todo.id"
                    :class="[todo.done? 'done': '']"
                >
                    <input
                        type="checkbox"
                        :id="'todo-'+ todo.id"
                        v-model="todo.done"
                    />
                    <label :for="'todo-'+ todo.id">{{ todo.text }}</label>
                    <a
                        @click="removeItem(todo)"
                        class="remove"
                    >&#215;</a>
                </li>
            </ul>
            <p v-else>Todo list is empty.</p>
        </div>
        <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
    name: 'App',
    // components: {
    //   HelloWorld
    // }
    data: function() {
        return {
            addText: 'buy a milk',
            todos: [
                {
                    id: 1,
                    text: 'Todo 1',
                    done: false,
                },
                {
                    id: 2,
                    text: 'Todo 2',
                    done: false,
                }
            ]
        }
    },
    methods: {
        getUid: function() {
            return Math.floor(Math.random() * 10000);
        },
        addItem: function() {

            if( this.addText ) {

                this.todos.push({
                    id: this.getUid(),
                    text: this.addText,
                    done: false,
                });

                this.addText = '';
            }
        },
        removeItem: function(todo){
            let _index = this.todos.indexOf(todo);

            if( _index > -1 ) {
                this.todos.splice(_index, 1);
            }
        },
    }
}
</script>

